h1 {
  font-size: 2em;
  color: #f09715;
}

p {
  font-size: 1.1em;
  font-family: sans-serif;
  color: white;
}

.header-container {
  display: flex;
  height: 120px;
  background-color: #f09715;
  flex-direction: column;
  z-index: 1;
}

#image-logo {
  width: 120px;
}

.first-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-button {
  width: 120px;
  height: 40px;
  border-radius: 10px;
  background-color: #f09715;
  border: 0px;
  font-size: 1.1em;
}

.header-button:hover {
  background-color: #d28311;
  color: white;
  font-size: 1.2em;
}

.header-button-left {
  width: 120px;
  height: 40px;
  border-radius: 10px;
  background-color: rgb(240, 151, 21);
  border: 0px;
  font-size: 1.1em;
}

.header-button-left:hover {
  background-color: #d28311;
  color: white;
  font-size: 1.2em;
}

.header-part-two {
  display: flex;
}

.hero-section {
  background-image: url(./images/esfirra-soja.jpeg);
  height: 100vh;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-hero {
  display: flex;
  color: #d28311;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(81, 47, 38, 0.8);
  width: 300px;
  height: 250px;
  border-radius: 10px;
  margin-bottom: 400px;
  margin-right: 50%;
}

.products-section {
  margin: 10px;
  display: flex;
  flex-direction: row;
  padding: 10px;
  border-radius: 10px;
  width: 450px;
  background-color: rgb(81, 47, 38, 0.8);
}

.image-addtocart {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.button-add-to-cart {
  border: none;
  background-color: #6f544d;
  margin-bottom: 20px;
}

.strong-price {
  font-size: 1.3em;
}

.Cardapio-items {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: rgb(235 235 235);
}

.image-products {
  border-radius: 10px;
  width: 100px;
  height: 100px;
}

.main-container {
  background-color: rgb(235 235 235);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-container-products {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.pizza-image-products {
  width: 100px;
  border-radius: 10px;
}

.products-section-pizzas {
  margin: 10px;
  display: flex;
  flex-direction: row;
  padding: 10px;
  border-radius: 10px;
  width: 450px;
  background-color: rgb(81, 47, 38, 0.8);
}

.products-section-sobremesas {
  margin: 10px;
  display: flex;
  flex-direction: row;
  padding: 10px;
  border-radius: 10px;
  width: 400px;
  background-color: rgb(81, 47, 38, 0.8);
}

.cardapio-class {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

@media screen and (max-width: 560px) {
  body {
    display: flex !important;
  }
  .header-container {
    display: flex;
    height: 100px;
    justify-content: space-between;
  }

  .first-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  #image-logo {
    width: 100px;
  }

  .section-links-header {
    display: flex;
    width: 60%;
    justify-content: flex-end;
  }

  .header-button {
    height: 40px;
    border-radius: 10px;
    background-color: rgb(240, 151, 21);
    border: 0px;
    font-size: 1.2em;
  }
  
  .header-button:hover {
    background-color: #d28311;
    color: white;
    font-size: 1.3em;
  }
  
  .header-button-left {
    height: 40px;
    border-radius: 10px;
    background-color: rgb(240, 151, 21);
    border: 0px;
    font-size: 1.2em;
  }
  
  .header-button-left:hover {
    background-color: #d28311;
    color: white;
    font-size: 1.3em;
  }

  .text-hero {
    display: flex;
    color: #d28311;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(81, 47, 38, 0.8);
    width: 300px;
    height: 250px;
    border-radius: 10px;
    margin-bottom: 200px;
  }
}